import React, {useState } from "react";
import { Container } from "react-bootstrap";
import UseAuth from "../../../Hooks/UseAuth";



const TableRows = ({ rows, tableRowRemove, onValUpdate}) => {

const {setStockSize} =UseAuth();
setStockSize(rows);

  return rows.map((rowsData, index) => {
    const { size_type, quantity} = rowsData;
    return (
      <>
      <tr key={index}>
        <td className="quantity_td">
        
          <input
            type="text"
            value={size_type}
            onChange={(event) => onValUpdate(index, event)}
            name="size_type"
            className="form-quantiy-field"
            placeholder="Size"
            required
          />

        </td>
        <td className="quantity_td">
         
          <input
            type="number"
            value={quantity}
            onChange={(event) => onValUpdate(index, event)}
            name="quantity"
            className="form-quantiy-field"
            required
            placeholder="Quantity"
          />

        </td>
        <td>
          <button
          type="button"
            className="btn btn-dark"
            onClick={() => tableRowRemove(index)}
          >
           X
          </button>
        </td>
      </tr>
      </>
    );
  });
};


const AddSizesTable =()=> {
  const [rows, initRow] = useState([]);
  
  const addRowTable = () => {
    const data = {
      size_type: "",
      quantity: ""
    };

    initRow([...rows, data]);
  };

  const tableRowRemove = (index) => {
    const dataRow = [...rows];
    dataRow.splice(index, 1);
    initRow(dataRow);
  };
  const onValUpdate = (i, event) => {
    const { name, value } = event.target;
    const data = [...rows];
    data[i][name] = value;
    initRow(data);
   
  };



  return (
    <>
    <Container className="mb-4">
      <h5 className="text-center pt-4">Add product size with quantity</h5>
       <div className="quantity_wrapper">
        <table className="table table-striped custome-table-size">
          <thead>
            <tr>
              <th>Size Type</th>
              <th>Quantity</th>
              <th>
                <button type="button" className="btn btn-danger" onClick={addRowTable}>
                  Add +
                </button>
              </th>
            </tr>
          </thead>
          <tbody>
            <TableRows
              rows={rows}
              tableRowRemove={tableRowRemove}
              onValUpdate={onValUpdate}
            />
          </tbody>
        </table>
       </div>
      </Container>
    </>
  );
};
export default AddSizesTable;