// import { Axios } from 'axios';
import React from "react";
import { useState } from "react";
import { Card, Container } from "react-bootstrap";
import ProgressBar from "react-bootstrap/ProgressBar";
import { useForm } from "react-hook-form";
import UseAuth from "../../Hooks/UseAuth";
import JoditEditor from "jodit-react";
import swal from "sweetalert";
import "./AddProduct.css";
import AddSizesTable from "./AddSizesTable/AddSizesTable";
import { useRef } from "react";

const AddProduct = () => {
  const [selectedFile, setSelectedFile] = useState();
  const [uploadProgress, setUploadProgress] = useState(0);
  const [productImg, setProductImg] = useState(null);
  const { setNum, stockSize } = UseAuth();
  const { register, handleSubmit, reset } = useForm();

  // Product description editor/
  const [content, setContent] = useState("");
  const editor = useRef(null);

  // product data add to the database(mongoDB)/
  const onSubmit = (data) => {
    // const size = data.sizeObject?.map(value=>{return value.value});
    // product data model//
    const product = {
      category: data.category,
      description: data.Product_description,
      img: productImg,
      name: data.productName,
      objectType: "product",
      price: data.price,
      stock: data.stock,
      size: stockSize,
      id: Math.floor(Math.random() * 1000000000),
    };

    // update form//
    console.log(product);

    fetch("https://api.femmewearbd.com/allproducts", {
      method: "POST",
      headers: {
        "content-type": "application/json",
      },
      body: JSON.stringify(product),
    })
      .then((res) => res.json())
      .then((result) => {
        if (result.acknowledged) {
          setNum((num) => num + 1);
          swal("Success!", "Product successfully Added", "success");
        }
        reset();
      });
  };

  // image uploader handler//
  // ===============================================
  const now = 100;
  const handleImageUpload = (event) => {
    const file = event.target.files[0];
    setSelectedFile(file);
    // Create a new FormData object
    const formData = new FormData();
    formData.append("image", file);

    // Make a POST request to the backend endpoint for file upload
    fetch("https://api.femmewearbd.com/uploadProductImg", {
      method: "POST",
      body: formData,
      // Report upload progress
      onUploadProgress: (progressEvent) => {
        const progress = Math.round(
          (progressEvent.loaded / progressEvent.total) * 100
        );
        setUploadProgress(progress);
      },
    })
      .then((response) => response.json())
      .then((data) => {
        console.log("File uploaded successfully", data);
        const Imgurl = data.imageUrl;

        // get image url after upload
        const url = Imgurl;
        setProductImg(url);
        console.log(url);
      })
      .catch((error) => {
        console.error("Error uploading file", error);
        // Handle error if the file upload faild
      });
    // console.log(selectedFile);
    event.preventDefault();
  };

  // use field for multi form//
  // const { fields, append, remove} = useFieldArray({
  //   control,
  //   name: "sizeObject",
  // });

  const allCategory = [
    "Bra Set",
    "Padded",
    "Non Padded",
    "Wireless",
    "Sports",
    "BodySuit/S.Suit",
    "Bikini",
    "Teenager Bra",
    "Mother Bra",
    "Lingerie",
    "Panty",
    "Nighty",
    "T-shirt",
    "Shorts",
    "Skirts",
    "Menstrual Cup",
    "Sanitary Napkin",
    "Other",
  ];

  // add product update version//
  // ===========================================
  // ===========================================

  //============================================
  //============================================

  return (
    <div className="dashboard-page">
      {/* <Container>
                <Card>
                  <form onSubmit={handleSubmit(onSubmit)}> <br />
                      <div className='product-add-form'>
                      
                      <input {...register("name", { required: true})} 
                        placeholder='Enter product Name'
                        className='add-in-field' />

                        <input type='file' {...register("img",{required:true})}
                        className='add-in-field'
                        onChange={(e) => handleImageUpload(e)}
                        />

                        <div className='add-in-field'>
                          {
                            productImg || selectedFile?<ProgressBar now={now} label={`${now}%`} />:<ProgressBar now={0} label={`${uploadProgress}%`} />
                          }
                        </div>


                        <select {...register("category",{required:true})}
                        className='add-in-field'>
                            <option >Select Category</option>
                            {
                              allCategory.map((itemsType,index)=><option 
                                key={index}
                                value={itemsType}>
                                {itemsType}
                              </option>)
                            }
                        </select>
                      
                        <input {...register("price", {required: true})} 
                        type='number'
                        placeholder='Give the price'
                        className='add-in-field'/> 

                        <textarea {...register("description", {required: true})} 
                        rows='4'
                        placeholder='description'
                        className='add-in-field'/>


                        <input type='number' {...register("stock",{required:true})} 
                        placeholder='stock'
                        className='add-in-field'/>

                        <div className='multi-form'>
                        <span>Available size:</span> <br />
                        {fields.map((field, index) => (
                          <input
                            key={field.id} // important to include key with field's id
                            className='size-field'
                            {...register(`sizeObject.${index}.value`,{required:true})} 
                          />
                        ))}  
                        <button 
                        type='button'
                          onClick={()=>append()}
                          className='add-btn' 
                        > +</button>          
                        </div>


                        <div className="size-clear-btn-container">
                          <button type='button' className='size-clear-btn' onClick={()=>remove()}> Remove Size</button>
                        </div>
                        
                        <input type="submit" className='submite-btn'/>

                      </div>
                      
                  </form>
                </Card>
            </Container> */}

      {/* Update version for add product// */}
      {/* ========================================== */}
      {/* ============================================== */}

      <div>
        <Container>
          <Card>
            <h3>Add a product</h3>

            <form onSubmit={handleSubmit(onSubmit)}>
              <div className="input_addProduct_container">
                {/* ======================================select catagory */}
                <div className="input_row">
                  <span>Select Category:</span>
                  <select
                    {...register("category", { required: true })}
                    className="product-input-field"
                  >
                    <option>Select Category</option>
                    {allCategory.map((itemsType, index) => (
                      <option key={index} value={itemsType}>
                        {itemsType}
                      </option>
                    ))}
                  </select>
                </div>

                {/* ======================================Product Name */}
                <div className="input_row">
                  <span>Product Name:</span>
                  <input
                    {...register("productName")}
                    required
                    placeholder="Product Name"
                    className="product-input-field"
                  />
                </div>

                {/* ====================================== Price */}
                <div className="input_row">
                  <span>Price (BDT):</span>
                  <input
                    {...register("price")}
                    required
                    placeholder="price"
                    type="number"
                    className="product-input-field"
                  />
                </div>
              </div>

              {/* ====================================== Product Img */}
              <div>
                <input
                  type="file"
                  {...register("img", { required: true })}
                  className="product-input-field"
                  onChange={(e) => handleImageUpload(e)}
                />

                <div className="progressBar">
                  {productImg || selectedFile ? (
                    <ProgressBar
                      className="progress_line"
                      now={now}
                      abel={`${now}%`}
                    />
                  ) : (
                    <ProgressBar now={0} label={`${uploadProgress}%`} />
                  )}
                </div>
              </div>

              {/* ====================================== Product Img */}
              <br />
              <h6>Write Product Description</h6>
              <div className="editor content_box m-0">
                <div className="description_editor text-start">
                  <JoditEditor
                    {...register("Product_description")}
                    ref={editor}
                    value={content}
                    tabIndex={1} // tabIndex of textarea
                    onBlur={(newContent) => setContent(newContent)} // preferred to use only this option to update the content for performance reasons
                    onChange={(newContent) => {}}
                  />
                </div>
              </div>

              {/* product stock managment component// */}
              <AddSizesTable />

              <input type="submit" className="btn btn-success mb-5" />
            </form>
          </Card>
        </Container>
      </div>

      {/* ========================================== */}
      {/* ============================================== */}
    </div>
  );
};

export default AddProduct;

//
