import React from "react";
import { useState } from "react";
import { Card, Col, Row } from "react-bootstrap";
import { useCart } from "react-use-cart";
import Footer from "../Shared/Footer/Footer";
import TopSection from "../Shared/TopSection";
import emptyBasket from "../../Assets/blankBasket.jpg";
import "./MyCart.css";
import UseAuth from "../Hooks/UseAuth";
import { useEffect } from "react";
import { useNavigate } from "react-router-dom";

const MyCart = () => {
  const [userCoupon, setUserCoupon] = useState("");
  const [disError, setDisError] = useState();
  const { cartItemsHandler, newOrderHandler } = UseAuth();

  // user info//
  const [district, setDistrict] = useState("none");
  const [userName, setUserName] = useState("");
  const [phoneNumber, setPhoneNumber] = useState("");
  const [userAddress, setUserAddress] = useState("");

  const {
    isEmpty,
    items,
    totalItems,
    cartTotal,
    updateItemQuantity,
    removeItem,
    totalUniqueItems,
  } = useCart();

  //   !important-------------------------total calculation
  const totalCosting = cartTotal + (district === "Dhaka" ? 70 : 140);
  // ------------discount price-------//
  const discount = 10;
  const discountprice = (discount / 100) * totalCosting;
  const costWithDiscount = totalCosting - discountprice;
  //----------coupon code--------------//
  const adminCopun = "inner2022";

  const payableAmount =
    adminCopun === userCoupon ? costWithDiscount : totalCosting;

  //-----------------order details//
  const order = {
    userName: userName,
    phoneNumber: phoneNumber,
    district: district,
    address: userAddress,
    payableAmount: payableAmount,
  };
  useEffect(() => {
    cartItemsHandler(totalUniqueItems);
  }, [cartItemsHandler, totalUniqueItems]);

  const allDistrict = [
    "Dhaka",
    "Faridpur",
    "Gazipur",
    "Gopalganj",
    "Jamalpur",
    "Kishoreganj",
    "Madaripur",
    "Manikganj",
    "Munshiganj",
    "Mymensingh",
    "Narayanganj",
    "Narsingdi",
    "Netrokona",
    "Rajbari",
    "Shariatpur",
    "Sherpur",
    "Tangail",
    "Bogura",
    "Joypurhat",
    "Naogaon",
    "Natore",
    "Nawabganj",
    "Pabna",
    "Rajshahi",
    "Sirajgonj",
    "Dinajpur",
    "Gaibandha",
    "Kurigram",
    "Lalmonirhat",
    "Nilphamari",
    "Panchagarh",
    "Rangpur",
    "Thakurgaon",
    "Barguna",
    "Barishal",
    "Bhola",
    "Jhalokati",
    "Patuakhali",
    "Pirojpur",
    "Bandarban",
    "Brahmanbaria",
    "Chandpur",
    "Chattogram",
    "Cumilla",
    "CoxBazar",
    "Feni",
    "Khagrachari",
    "Lakshmipur",
    "Noakhali",
    "Rangamati",
    "Habiganj",
    "Maulvibazar",
    "Sunamganj",
    "Sylhet",
    "Bagerhat",
    "Chuadanga",
    "Jashore",
    "Jhenaidah",
    "Khulna",
    "Kushtia",
    "Magura",
    "Meherpur",
    "Narail",
    "Satkhira",
  ];
  const districtErrorHadler = () => {
    setDisError("Please select Your District");
  };

  const history = useNavigate();

  const submitHandler = (e, items, order) => {
    e.preventDefault();
    console.log(items);
    newOrderHandler(items, order, history);
  };
  console.log(items);

  return (
    <div>
      <TopSection />
      {isEmpty ? (
        <div className="container p-5">
          <img
            className="img-fluid empty-basket"
            src={emptyBasket}
            alt="femmewear-product"
          />
          <h3>There is no item in your cart</h3>
        </div>
      ) : (
        <section className="container py-4">
          <h3>
            Cart ({totalUniqueItems}), total items: ({totalItems})
          </h3>
          <Row>
            <Col sm={12} md={6}>
              {items.map((item, index) => (
                <Card className="my-4 pb-2 item-card" key={index}>
                  <Row className="p-2 text-start row-cols-sm-2">
                    <Col className="col-sm-4 img-product">
                      <img
                        className="img-fluid"
                        src={item.img}
                        alt="femmewear-product"
                      />
                    </Col>

                    <Col className=" text-start fs-5 fw-bold">
                      <h4>{item.name}</h4>
                      <h5>Price: {item.price} BDT</h5>
                      <h6>Size: {item.userSize}</h6>
                    </Col>
                    <Col sm={6} md={6} lg={4}>
                      <span className="me-2 fs-5 fw-bold">
                        Quantity : {item.quantity}
                      </span>{" "}
                      <br />
                      <button
                        className="update-btn"
                        onClick={() => {
                          const sizeIndex = item?.size?.find(
                            (element) => element?.size_type === item?.userSize
                          );
                          sizeIndex.quantity = parseInt(sizeIndex.quantity) + 1;
                          updateItemQuantity(item.id, item.quantity - 1);
                        }}
                      >
                        -
                      </button>
                      <button
                        className="update-btn"
                        onClick={() => {
                          const sizeIndex = item?.size?.find(
                            (element) => element?.size_type === item?.userSize
                          );
                          if (parseInt(sizeIndex.quantity) > 0) {
                            sizeIndex.quantity =
                              parseInt(sizeIndex.quantity) - 1;
                            updateItemQuantity(item.id, item.quantity + 1);
                          }
                        }}
                      >
                        +
                      </button>
                    </Col>
                    <button
                      onClick={() => removeItem(item.id)}
                      className="remove-from-cart-btn"
                    >
                      &#10060;
                    </button>
                  </Row>
                </Card>
              ))}
            </Col>
            <Col sm={12} md={6}>
              <Card className="my-4">
                <div className="px-2 py-4">
                  <form onSubmit={(e) => submitHandler(e, items, order)}>
                    <Row className="text-start">
                      <Col sm={12} md={6} className="my-2">
                        <span>District: </span>
                        <select
                          name="bd_districts"
                          onChange={(e) => setDistrict(e.target.value)}
                          required
                          className="user-input"
                        >
                          <option value="none">Select your Districts</option>
                          {allDistrict.map((bddistrict, index) => (
                            <option key={index} value={bddistrict}>
                              {bddistrict}
                            </option>
                          ))}
                        </select>
                      </Col>
                      <Col sm={12} md={6}>
                        {/* Delivery Cost: <input type="number" value={district !== 'Dhaka'? 140:70}/>&#2547; */}
                        Delivery Cost: {district === "Dhaka" ? 70 : 140} &#2547;
                      </Col>
                      <p className="text-danger">{disError}</p>
                      <Col sm={12} className="my-2">
                        Address:
                        <input
                          type="text"
                          placeholder="#house, road, area"
                          required
                          className="shipping-address-field user-input"
                          onChange={(e) => setUserAddress(e.target.value)}
                        />
                      </Col>
                      <Col sm={12} md={6} className="my-2">
                        Name:
                        <input
                          className="user-input ms-1"
                          type="text"
                          placeholder="Enter Your Name"
                          required
                          onChange={(e) => setUserName(e.target.value)}
                        />
                      </Col>
                      <Col sm={12} md={6} className="my-2">
                        Phone :
                        <input
                          type="number"
                          placeholder="Enter Your Phone Number"
                          required
                          className="user-input"
                          onChange={(e) => setPhoneNumber(e.target.value)}
                        />
                      </Col>
                      <Col sm={12}>
                        <h5 className="text-primary">
                          {" "}
                          Payable Amount :BDT {payableAmount} &#2547;
                        </h5>
                        <h4>Discount Coupon:</h4>
                        <input
                          type="text"
                          className="cuponfield"
                          onChange={(e) => setUserCoupon(e.target.value)}
                          placeholder="coupon"
                        />{" "}
                        <br /> <br />
                      </Col>

                      <div className="submit-btn-container">
                        {district === "none" ? (
                          <p
                            className="submit-btn text-center py-1"
                            onClick={() => districtErrorHadler()}
                          >
                            Submit
                          </p>
                        ) : (
                          <input className="submit-btn" type="submit" />
                        )}
                      </div>
                    </Row>
                  </form>
                </div>
              </Card>
            </Col>
          </Row>
        </section>
      )}
      <Footer />
    </div>
  );
};

export default MyCart;
