import React from 'react';
import TopSection from '../../Shared/TopSection';
import { Card, Col, Container, Row } from 'react-bootstrap';
import { Link } from 'react-router-dom';
import UseAuth from '../../Hooks/UseAuth';
import Footer from '../../Shared/Footer/Footer';
import MessengerChat from '../../Shared/MessengerChat/MessengerChat';
import AdSenseCardAdds from '../../../AdsenseAddsCard/AdSenseCardAdds';
import { useEffect } from 'react';
import { useState } from 'react';
const Skirts = () => {
    const {allProduct} = UseAuth();
    const [skrits, setSkrits] = useState()
  

    useEffect(()=>{
        const skirtsItem = allProduct?.filter(function(product) {
        return product?.category === "Skirts";
      });
      setSkrits(skirtsItem);
    },[allProduct])
    
    // const itemStock = skrits[0].size;

    // const newStock = itemStock?.filter(item => item?.quantity!==1);
    // console.log("new arry", newStock)

    return (
        <div>
            
               <TopSection/>
             <div>
         <Container>
         <Row xs={2} md={4} className="g-4">
              {skrits?.map((skirts,index) => (
                  <Col key={index}>
                 <Card className='product-card pb-2'>
                 <Link to={`/details/${skirts._id}`} className='order-link'>
                        <Card.Img variant="top" src={skirts.img} className="img-fluid" />
                    </Link>
                            <div className='text-start product-size'>
                            <div>
                                <div className='card-price'>
                                    <p>Available size:</p><p className='bdt-show'>&#2547; {skirts?.price}</p>
                                </div>
                                 <div className='size-box'>
                                    {
                                      skirts?.size?.map((sizes,index)=><div
                                      key={index}>
                                      <span>{sizes?.size_type}</span>    
                                         
                                    </div>)
                                     }
                                 </div>
                                    {/* <div>
                                        {
                                           itemStock>=1?
                                           <span className='text-success fw-semibold'>Avilable in stock</span>:
                                           <span className='text-danger fw-semibold'>out of Stock</span>
                                        }
                                     </div> */}
                            </div>
                           
                            <button className='buynow-btn mt-2'><Link to={`/details/${skirts._id}`} className='order-link'>Order Now</Link></button>
                            </div>

                                 
                        </Card>
                  </Col>
              ))}
              </Row>


               {/* Google adds// */}
               <AdSenseCardAdds/>
         </Container>
          </div>
          <Footer/>
          <MessengerChat/>
        </div>
    );
};

export default Skirts;